import { Emojione } from 'react-emoji-render';
import { useState, useEffect } from 'react';

export const Emoji = ({text, flipped, className}) => (
    <Emojione svg className={className + (flipped ? " flip-horizontally" : "")} text={text}/>
);

export const LouBricant = () => {
    const [ lookRight, setLookRight ] = useState(false);
    const [ lookInterval, setLookInterval ] = useState(() => () => {});

    const rate = 2000;

    useEffect(() => {
        const interval = setInterval(() => {
            setLookRight(!lookRight);
        }, rate);
        setLookInterval(interval);
        return () => clearInterval(lookInterval);
    }, [lookRight]);

    return (
        <div className="lou-bricant">
            <Emoji text=":fire:"/><br/>
            <Emoji text=":eyes:" flipped={!lookRight}/><br/>
            <Emoji text=":nose:"/><br/>
            <Emoji text=":mouth:"/><br/>
            <Emoji text="|"/><br/>
            <Emoji text=":wave:"/><span className="whitespace">___/\___</span><Emoji text=":wave:" flipped/><br/>
        </div>
    );
}

export function InfoBox({children}) {
    return (
        <div className="info-box">
            <table>
                <tbody>
                    <tr>
                        <td className="info-box-i-column">
                            <span className="info-box-i">ℹ️</span>
                        </td>
                        <td className="info-box-content-column">
                            {children}
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}

export const emitCurrentPathUpdatedEvent = currentPath => {
    document.dispatchEvent(new CustomEvent("current-path-updated", {detail: currentPath}));
};
