import React, { useState, useEffect } from 'react';
import { Link, useParams, Redirect } from "react-router-dom";
import ReactMarkdown from 'react-markdown'
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import * as codeStyles from 'react-syntax-highlighter/dist/esm/styles/prism'
import okaidiaRight from '../okaidaRight'
import { emitCurrentPathUpdatedEvent } from '../common';

const renderers = {
    code: ({language, value}) => {
        if (value.startsWith("---ALIGN-RIGHT---\n")) {
            return <SyntaxHighlighter style={okaidiaRight} language={language} children={value.substring(18,)} />
        } else {
            return <SyntaxHighlighter style={codeStyles.okaidia} language={language} children={value} />
        }
    }
}

export function BlogOverviewPage() {
    const [ postIds, setPostIds ] = useState([]);
    useEffect(() => emitCurrentPathUpdatedEvent("/blog"));
    useEffect(() =>
        fetch("/blog-posts/index.json")
              .then(response => response.json())
              .then(setPostIds),
        []
    );
    return (
        <div className="content centered-content">
            <h1>Blog Posts</h1>
            { postIds.map(id => <BlogPostPreview key={id} id={id}/>) }
        </div>
    );
}

function BlogPostPreview({id}) {
    const [ metadata, setMetadata ] = useState(emptyMetadata());
    const [ content, setContent ] = useState("");

    useEffect(() =>
        fetch(`/blog-posts/${id}/metadata.json`)
            .then(response => response.json())
            .then(setMetadata),
        []
    );

    useEffect(() =>
        fetch(`/blog-posts/${id}/content.md`)
            .then(response => response.text())
            .then(setContent),
        []
    );

    return (
        <div className="blog-post-preview">
            <Link to={`blog/${id}`}>
                <h2>{metadata.title}</h2>
            </Link>
            <p className="blog-post-preview-content">
                {shortenBlogPostContentForPreview(content)}
            </p>
            <p><strong>Created on {metadata.date}</strong></p>
        </div>
    );
}

function emptyMetadata() {
    return {
        "title": "",
        "date": "",
        "tags": [],
    };
}

export function BlogPostPage() {
    const { id } = useParams();

    const [ metadata, setMetadata ] = useState(emptyMetadata());
    const [ content, setContent ] = useState("");
    const [ foundBlogPost, setFoundBlogPost ] = useState(true);

    const addMetaTag = (attributes) => {
        const meta = document.createElement('meta');
        for (const [key, value] of Object.entries(attributes)) {
            console.log("key:" + key + " value:" + value);
            meta[key] = value;
        }
        document.getElementsByTagName('head')[0].appendChild(meta);
    };

    useEffect(() => emitCurrentPathUpdatedEvent("/blog"));

    useEffect(() =>
        fetch("/blog-posts/index.json")
              .then(response => response.json())
              .then(postIds => {
                  if (!postIds.includes(id)) {
                      setFoundBlogPost(false);
                  }
              }),
        []
    );

    useEffect(() =>
        fetch(`/blog-posts/${id}/metadata.json`)
            .then(response => response.json())
            .then(metadata => {
                addMetaTag({property: "twitter:card", content: "summary_large_image"});
                addMetaTag({property: "twitter:title", content: `${metadata.title} - Byxor's Corner`});
                return setMetadata(metadata);
            }),
        []
    );

    useEffect(() =>
        fetch(`/blog-posts/${id}/content.md`)
            .then(response => response.text())
            .then(setContent),
        []
    );

    return (
        <div className="content centered-content">
            {!foundBlogPost && <Redirect to="/blog"/>}
            <h1><Link to="/blog">Blog/ </Link>{metadata.title}</h1>
            {content !== "" && (<div className="blog-post-page-credits">
                <img className="blog-post-page-credits-icon" src="/me.jpeg"/>
                <div className="vertically-centered">
                    <span className="date">Published by <strong>Brandon Ibbotson</strong> on <strong>{metadata.date}</strong></span>
                </div>
            </div>)}
            <div className="blog-post-page-content">
                <ReactMarkdown renderers={renderers} allowDangerousHtml>{content}</ReactMarkdown>
            </div>
        </div>
    );
}

function shortenBlogPostContentForPreview(content) {
    const maxLength = 300;
    const shortened = content.length > maxLength ? content.substring(0, maxLength) + "..." : content;
    const sanitized = shortened.replace(/\*/g, "").replace(/_/g, "");
    return sanitized;
}

