import { emitCurrentPathUpdatedEvent } from '../common';
import { useEffect } from 'react';

export function ContentNotFoundPage() {
    useEffect(() => emitCurrentPathUpdatedEvent("/404"));
    return (
        <div className="content centered-content">
            <h1>404 (Not Found)</h1>
            <img src="/404.gif" alt="Monkey pushing laptop away in frustration"/>
            <p>Whatever content you're looking for, I can't find it 🤷</p>
        </div>
    );
}
