import './App.css';
import React, { useState, useEffect } from 'react';
import { ProjectsOverviewPage } from './pages/Projects';
import { ContentNotFoundPage } from './pages/NotFound';
import { BlogOverviewPage, BlogPostPage } from './pages/Blog';
import { DemosOverviewPage } from './pages/Demos';
import { TetrisDemoPage } from './pages/demos/Tetris';
import { ContactPage } from './pages/Contact';
import { HomePage } from './pages/Home';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

function App() {
  return (
      <>
          <Router>
              <NavBar/>
              <RoutedBody/>
          </Router>
      </>
  );
}

function NavBar() {
    const [ currentPath, setCurrentPath ] = useState("/");
    const underConstruction = false;

    useEffect(() => {
        document.addEventListener("current-path-updated", event => {
            setCurrentPath(event.detail);
        });
    }, []);

    return (
        <div className="nav-bar-wrapper">
            <div className="nav-bar">
                <NavBlinkingName/>
                <br/>
                {underConstruction && <img className="construction" src="/construction.gif" alt="Website under construction"/>}
                <br/>
                <NavLink text="Home" path="/" currentPath={currentPath}/>
                <NavLink text="Blog" path="/blog" currentPath={currentPath}/>
                <NavLink text="Demos" path="/demos" currentPath={currentPath}/>
                <NavLink text="Projects" path="/projects" currentPath={currentPath} className="work-in-progress"/>
                <NavLink text="Contact" path="/contact" currentPath={currentPath} className="work-in-progress"/>
            </div>
        </div>
    );
}

function NavLink({text, path, currentPath, className}) {
    return (
        <Link to={path} className={"nav-link-wrapper" + (path === currentPath ? " nav-link-button-selected" : "")}>
            <input type="button" className={"nav-link-button " + className} value={text} />
        </Link>
    );
}

function NavBlinkingName() {
    return (
        <div className="nav-blinking-name">
            <div>
                <img className="nav-bar-icon" src="/nav-bar-icon.jpg" alt="Main website icon"/>
                <div>
                    <span>Byxor.xyz</span><span className="blinking">_</span>
                </div>
            </div>
        </div>
    );
}

function RoutedBody() {
    return (
        <Switch>

            <Route exact path="/blog/:id">
                <BlogPostPage/>
            </Route>
  
            <Route exact path="/blog">
                <BlogOverviewPage/>
            </Route>
  
            <Route exact path="/projects">
                <ProjectsOverviewPage/>
            </Route>
  
            <Route exact path="/demos/classic-tetris-enhanced">
                <TetrisDemoPage/>
            </Route>

            <Route exact path="/demos">
                <DemosOverviewPage/>
            </Route>
  
            <Route exact path="/contact">
                <ContactPage/>
            </Route>
  
            <Route exact path="/">
                <HomePage/>
            </Route>
 
            <Route path="*">
              <ContentNotFoundPage/>
            </Route>
        </Switch>
    )
}

export default App;
